@use '../util' as *;

.button_grid {
  padding: rem(17) rem(16);
  position: fixed;
  top: rem(128);
  display: grid;
  width: rem(301);
  height: rem(184);
  left: rem(48);
  grid-template-columns: rem(269);
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: rem(27);
  z-index: 1;

  &__button {
    position: relative;
    overflow: hidden;
    background: none;
    width: rem(269);
    height: rem(32);
    border: none;
    transition: background 150ms ease-in-out;
  
    &:hover {
      background: hsla(0, 100%, 100%, 0.2);
    }
  }
}