@use '../util/' as *;

h1,
h2,
h3 {
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  margin-bottom: rem(3);
  font-size: rem(32);
  @include breakpoint(medium) {
    font-size: rem(28);
  }
}

h2 {
  font-size: rem(16);
  font-weight: 700;
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}
