@use '../util' as *;

.header {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: rem(32) rem(32);
  align-items: center;

  &__logo {
    width: rem(64);
    height: rem(64);
    border: rem(2) solid black;
  }

  &__title {
    width: 100%;
    height: 100%;
    font-size: rem(32);
    font-weight: 700;
    -webkit-text-stroke-width: rem(1.5);
    -webkit-text-stroke-color: black;
    color: var(--text-color);
    margin-left: rem(24);
  }

  &__subtitle {
    font-size: rem(14);
    font-weight: 700;
  }
}