@use '../util' as *;
// https://codepen.io/SaraSoueidan/pen/jpBbrq/?editors=1100

.toggle {
  display: grid;
  grid-template-columns: 1fr rem(48);
  border: none;
  margin: 0;

  label {
    font-size: rem(14);
    font-weight: 700;
    color: var(--text-color2);
    justify-self: right;

    &[for='dark'] {
      line-height: rem(24);
      margin-right: rem(13);
    }
  }

  &__wrapper {
    position: relative;
    height: rem(24);
  }

  input[type='radio'] {
    opacity: 0;
    margin: 0 rem(-2) 0 rem(-1);
    height: rem(24);
    width: rem(24);

    &:focus ~ .toggle__button {
      border: rem(2) solid var(--light-top-bg);
    }
  }

  &__background {
    display: block;
    height: 100%;
    position: absolute;
    width: 100%;
    top: 0;
    border-radius: rem(12);
    overflow: hidden;
    pointer-events: none;
    background: var(--toggle-bg);
  }

  &__button {
    position: absolute;
    left: rem(4);
    top: rem(3);
    right: 100%;
    width: rem(18);
    height: rem(18);
    border-radius: 50%;
    background-color: var(--toggle-button);
    transition: all 150ms ease-in-out;
  }

  #light:checked ~ .toggle__button {
    right: rem(4);
    left: calc(100% - rem(22));
  }
}
