// 640px, 1281px, 1400px
$breakpoints-up: (
  'medium': '40em',
  'large': '80.0625em',
  'xlarge': '87.5em',
);

// 639px, 1279px, 1399px
$breakpoints-down: (
  'small': '39.9375em',
  'medium': '79.9375em',
  'large': '87.4375em',
);

@mixin breakpoint($size) {
  @media (min-width: map-get($breakpoints-up, $size)) {
    @content;
  }
}

@mixin breakpoint-down($size) {
  @media (max-width: map-get($breakpoints-down, $size)) {
    @content;
  }
}
