@use '../util' as *;

:root {
  --sizing-parameter: max(100vw, 100vh);
}

.container {
  padding: 0 rem(32);
  max-width: 100vh;
  margin: 0 0 0 rem(32);
  display: block;
  align-content: center;
  position: absolute;

  @include breakpoint(large) {
    padding: 0;
  }

  &__body {
    padding: 0;
    top: rem(96);
    width: calc(100vw - rem(32));
    height: calc(100vh - rem(96));
  }
}

.sketch_container {
  position: absolute;
  top: 0;
  bottom: 0;
  display: block;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  border: none;

  &__frame {
    position: relative;
    top: rem(-42);
    bottom: 0;
    display: block;
    width: 100vw;
    height: calc(100vh + rem(42));
    border: none;
  }
}

.link_tree {
  padding: rem(17) rem(16);
  position: fixed;
  top: rem(128);
  display: grid;
  width: rem(301);
  height: rem(184);
  left: rem(48);
  grid-template-columns: rem(32) rem(210);
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: rem(27);
  background: hsla(0, 100%, 0%, 0.6);

  &__icon {
    position: relative;
    display: flex;
    width: rem(32);
    height: rem(32);
    align-items: center;
    text-align: center;
    justify-self: end;
    color: white;
  }

  &__item {
    position: relative;
    display: flex;
    align-items: center;
    justify-self: start;
    color: white;
  }
}

.copyright_box {
  position: fixed;
  bottom: rem(32);
  left: rem(32);
  display: grid;
  width: rem(256);
  height: rem(48);
  grid-template-columns: rem(32) rem(136);
  grid-template-rows: 1fr;
  gap: rem(4);
  background: hsla(0, 100%, 0%, 0.6);
  align-items: top;
  justify-content: center;

  &__icon {
    width: rem(32);
    height: rem(58);
    padding-top: rem(2);
    justify-self: end;
    font-size: rem(48);
    color: white;
  }

  &__item {
    padding-top: rem(15);
    justify-self: start;
    color: white;
  }
}
