:root {
  --font-b612: "B612 Mono";
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 70%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --text-color: white;
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--light-bg);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end),
      var(--toggle-bg-start)
    );
    --toggle-button: var(--dark-bg);
  }
}
@media (prefers-color-scheme: light) {
  :root {
    --background: var(--light-bg);
    --text-color: var(--light-text2);
    --text-color2: var(--light-text1);
    --card-bg: var(--light-card);
    --card-hover: var(--light-card-hover);
    --toggle-bg: var(--toggle-bg-light);
    --toggle-button: var(--light-bg);
  }
}
body.dark {
  --background: var(--dark-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end),
    var(--toggle-bg-start)
  );
  --toggle-button: var(--dark-bg);
}

body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--light-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-b612);
  background: var(--background);
  color: var(--text_color);
  min-height: 100vh;
}

h1,
h2,
h3 {
  line-height: 1.1;
  margin-top: 0;
}

h1 {
  margin-bottom: 0.1875rem;
  font-size: 2rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1rem;
  font-weight: 700;
  margin: 0;
  padding: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --sizing-parameter: max(100vw, 100vh);
}

.container {
  padding: 0 2rem;
  max-width: 100vh;
  margin: 0 0 0 2rem;
  display: block;
  align-content: center;
  position: absolute;
}
@media (min-width: 80.0625em) {
  .container {
    padding: 0;
  }
}
.container__body {
  padding: 0;
  top: 6rem;
  width: calc(100vw - 2rem);
  height: calc(100vh - 6rem);
}

.sketch_container {
  position: absolute;
  top: 0;
  bottom: 0;
  display: block;
  z-index: -1;
  width: 100vw;
  height: 100vh;
  margin: 0;
  overflow: hidden;
  border: none;
}
.sketch_container__frame {
  position: relative;
  top: -2.625rem;
  bottom: 0;
  display: block;
  width: 100vw;
  height: calc(100vh + 2.625rem);
  border: none;
}

.link_tree {
  padding: 1.0625rem 1rem;
  position: fixed;
  top: 8rem;
  display: grid;
  width: 18.8125rem;
  height: 11.5rem;
  left: 3rem;
  grid-template-columns: 2rem 13.125rem;
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: 1.6875rem;
  background: hsla(0, 100%, 0%, 0.6);
}
.link_tree__icon {
  position: relative;
  display: flex;
  width: 2rem;
  height: 2rem;
  align-items: center;
  text-align: center;
  justify-self: end;
  color: white;
}
.link_tree__item {
  position: relative;
  display: flex;
  align-items: center;
  justify-self: start;
  color: white;
}

.copyright_box {
  position: fixed;
  bottom: 2rem;
  left: 2rem;
  display: grid;
  width: 16rem;
  height: 3rem;
  grid-template-columns: 2rem 8.5rem;
  grid-template-rows: 1fr;
  gap: 0.25rem;
  background: hsla(0, 100%, 0%, 0.6);
  align-items: top;
  justify-content: center;
}
.copyright_box__icon {
  width: 2rem;
  height: 3.625rem;
  padding-top: 0.125rem;
  justify-self: end;
  font-size: 3rem;
  color: white;
}
.copyright_box__item {
  padding-top: 0.9375rem;
  justify-self: start;
  color: white;
}

.header {
  position: fixed;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 2rem 2rem;
  align-items: center;
}
.header__logo {
  width: 4rem;
  height: 4rem;
  border: 0.125rem solid black;
}
.header__title {
  width: 100%;
  height: 100%;
  font-size: 2rem;
  font-weight: 700;
  -webkit-text-stroke-width: 0.09375rem;
  -webkit-text-stroke-color: black;
  color: var(--text-color);
  margin-left: 1.5rem;
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text-color2);
  justify-self: right;
}
.toggle label[for=dark] {
  line-height: 1.5rem;
  margin-right: 0.8125rem;
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem 0 -0.0625rem;
  height: 1.5rem;
  width: 1.5rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 0.125rem solid var(--light-top-bg);
}
.toggle__background {
  display: block;
  height: 100%;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  pointer-events: none;
  background: var(--toggle-bg);
}
.toggle__button {
  position: absolute;
  left: 0.25rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.25rem;
  left: calc(100% - 1.375rem);
}

.button_grid {
  padding: 1.0625rem 1rem;
  position: fixed;
  top: 8rem;
  display: grid;
  width: 18.8125rem;
  height: 11.5rem;
  left: 3rem;
  grid-template-columns: 16.8125rem;
  grid-template-rows: repeat(3, 1fr);
  align-items: center;
  gap: 1.6875rem;
  z-index: 1;
}
.button_grid__button {
  position: relative;
  overflow: hidden;
  background: none;
  width: 16.8125rem;
  height: 2rem;
  border: none;
  transition: background 150ms ease-in-out;
}
.button_grid__button:hover {
  background: hsla(0, 100%, 100%, 0.2);
}